import React from "react"

import Layout from '../components/layout'
import SEO from '../components/seo'
import {Link, graphql} from 'gatsby'
import Img from "gatsby-image"

import '../styles/blog.scss'

export default function BlogPage({data}) {

  const {postsQuery: posts} = data;

  return (
    <Layout>
      <SEO title="Blog" description="Mage Mastery Blog with the latest news, online courses announcements"/>
      <div className="courses-header blog-header pb-5 pt-5">
        <div className="container">
          <h1>Blog</h1>
        </div>
      </div>
      <div className="container-fluid mt-5 mb-5">
        {posts.edges.map((item) => {
          const post = item.node
          const image = post.frontmatter.featured_image ? post.frontmatter.featured_image.childImageSharp.fluid : null

          return (<div className="post-container container mt-5 mb-5" key={post.id}>
            <Link to={post.frontmatter.path}><h3>{post.frontmatter.title}</h3></Link>
            <div className="date-author mb-2">{post.frontmatter.author} | <span className="date">{post.frontmatter.date}</span></div>
            {image !== null ? <Img fluid={image} /> : ''}
            <div className="mt-3 mb-3">
              <div dangerouslySetInnerHTML={{__html: post.frontmatter.short_description}} />
            </div>
            <Link to={post.frontmatter.path}>Continue Reading</Link>
          </div>)
        })}
      </div>
    </Layout>
  )
}

export const pageQuery = graphql`
  query BlogQuery {
    postsQuery: allMarkdownRemark(
      filter: { 
        frontmatter: { 
          type: {eq: "blog"}
        }
      },
      sort: {
        fields: [frontmatter___date],
        order: DESC
      }
    ) {
      edges {
        node {
          id
          html
          frontmatter {
            title
            path
            description
            author  
            short_description
            date(formatString: "MMMM DD, YYYY")
            featured_image {
              childImageSharp {
                fluid(maxWidth: 800) {
                  ...GatsbyImageSharpFluid
                }
              }
            }
          }
        }
      }
    }
  }
`
